<script>
import Recipes from '../services/recipes'

export default{
    data() {
        return {
            arrayView: [],
            recipes: [],
            json: {},
        }
    },
    methods: {
      ordenarPorVida(){
        this.arrayView = this.arrayView.sort((a, b) => parseFloat(b.health) - parseFloat(a.health));
      },
      ordenarPorSanidade(){
        this.arrayView = this.arrayView.sort((a, b) => parseFloat(b.sanity) - parseFloat(a.sanity));
      },
      ordenarPorFome(){
        this.arrayView = this.arrayView.sort((a, b) => parseFloat(b.hunger) - parseFloat(a.hunger));
      },
      mostrarSomenteDlc(dlc){
        this.arrayView = []

        for (var i = 0; i < this.recipes.length; i++) {
          if (this.recipes[i].arrayDlcs.includes(dlc)){
              this.arrayView.push(this.recipes[i]);
          }
        }
      },
      mostrarTodos(){
        this.arrayView = this.recipes
      },
      retornarImagemDlc(dlc){
        if (dlc === "Don't Starve Together"){
          return 'https://vignette.wikia.nocookie.net/dont-starve-game/images/b/b0/Don%27t_Starve_Together_icon.png'
        }
        if (dlc === "Hamlet"){
          return 'https://vignette.wikia.nocookie.net/dont-starve-game/images/0/09/Hamlet_icon.png'
        }
        if (dlc === "Shipwrecked"){
          return 'https://vignette.wikia.nocookie.net/dont-starve-game/images/4/46/Shipwrecked_icon.png'
        }
        if (dlc === "Reign of Giants"){
          return 'https://vignette.wikia.nocookie.net/dont-starve-game/images/f/f8/Reign_of_Giants_icon.png'
        }
      }
    },
    mounted(){
        Recipes.retornar().then(resposta =>{
          this.json = resposta.data
          this.recipes = this.json.recipes;
          this.arrayView = this.recipes
        })
    }
}
</script>
